import axios from 'axios'
const productionUrl = 'https://react-blogs-server.herokuapp.com/api'
// const testUrl = 'https://localhost:3000/api'
const topic = '/login'
const url = productionUrl + topic;

const getUser = () => {
  try {
    return JSON.parse(window.localStorage.user)
  } catch (error) {
    return null
  }
}

const getToken = () => `bearer ${getUser().token}`

const store = user => {
  window.localStorage.user = JSON.stringify(user)
}

const login = async credentials => {
  const user = (await axios.post(url, credentials)).data
  store(user)
  return user;
}

const logout = () => {
  window.localStorage.clear()
}

export default { login, logout, getUser, getToken }